<template>
<div class="wrapper">
  <div class="login_container">
    <el-card class="login_box">
      <!--  -->
      <div class="box-head">
        <h1 style="text-align:center;color:#333333">MES</h1>
      </div>
      <!--  -->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules"  class="login_form"  label-width="80px" label-position="left">      
        <!--  -->
        <el-form-item prop="username" label="">
          <el-input v-model="loginForm.username" prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password" label="">
          <el-input v-model="loginForm.password" prefix-icon="iconfont icon-3702mima" type="password"></el-input>
        </el-form-item>
        <el-form-item label=" ">
          <el-select v-model="loginForm.system" placeholder="" style="with:300px;">
            <el-option label="MES" value="shanghai"></el-option>
            <el-option label="ERP" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</div>
  
</template>

<script>
export default {
  data() {
    return {
      // 
      loginForm: {
        username: 'admin',
        password: '123456',
        system:'',
      },
      // 
      loginFormRules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        // 
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 
    resetLoginForm() {
      // console.log(this);
      this.$refs.loginFormRef.resetFields()
    },
    login() {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return
        // const { data: res } = await this.$http.post('login', this.loginForm)
        // if (res.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        // 1.  token sessionStorage 
        //   1.1 API
        //   1.2 token  token  sessionStorage 
        // window.sessionStorage.setItem('token', res.data.token)
        // 2.  /home
        this.$router.push('/home')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.wrapper{
  width: 100%;
  height: 100%;
  position: relative;
}
.login_container {
  width: 100%;
  box-sizing: border-box;
  height: 50vh;
  background-color: #6e00ff;
  border-bottom-right-radius: 40px;
  border-bottom-left-radius: 40px;
}
.login_box {
  padding: 0 !important;
  width: 450px;
  height: 500px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.box-head{
  padding: 30px 0;
}
.login_form {
  position: absolute;
  bottom: 45px;
  width: 90%;
  padding: 0 20px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: flex-end;
  padding-top: 30px;
}
.el-select{
  width: 100%;
}
</style>
